        emit('setBuyRate95', data.setBuyRate95Value)
      },
      setbuyFundRate95: () => {
        emit('setbuyFundRate95', data.setbuyFundRate95Value)
      },
      setTransferRate95: () => {
        emit('setTransferRate95', data.setTransferRate95Value)
      },
      settransferFundRate95: () => {
        emit('settransferFundRate95', data.settransferFundRate95Value)
      },
      setFundRate95: () => {
        emit('setFundRate95', data.setFundRate95Value)
      },
      setLiquidityRate95: () => {
        emit('setLiquidityRate95', data.setLiquidityRate95Value)
      },
      setTeRate95: () => {
        emit('setTeRate95', data.setTeRate95Value)
      },
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
